import StopWatch from "../components/StopWatch/StopWatch";
import Timer from "../components/StopWatch/Timer";
import { observer, inject } from "mobx-react";
import React, { useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";

function Admin({ TimerStore, TeamStore }) {
  const [gender, setGender] = useState("First");

  function onChangeValue(event) {
    setGender(event.target.value);
    TeamStore.setTry(event.target.value);
    console.log(event.target.value);
  }

  return (
    <div class="container px-4">
        <Timer />
      <div class="row d-flex justify-content-center">
        <div class="col p-3 d-flex justify-content-center">
          <h2>команда:</h2>
        </div>
        <div class="col p-3 d-flex justify-content-center">
          <input
            type="text"
            value={TeamStore.getTeamName}
            onChange={(e) => TeamStore.setTeamName(e.target.value)}
          />
        </div>
        <div class="row d-flex justify-content-center">
          <div class="col p-3 d-flex justify-content-center">
            <button   onClick={(e) => TeamStore.hideTeam()}>Скрыть команду</button>
          </div>
          <div class="col p-3 d-flex justify-content-center">
            <button type="button"onClick={(e) => TeamStore.showTeam()}>Показать команду</button>
          </div>
        </div>
      </div>
      <div class="row d-flex justify-content-center">
        <div class="col p-3 d-flex justify-content-center">
          <h3>текущая попытка:</h3>
          </div>
          <div class="col p-3 d-flex justify-content-center">
          <h3>
            <div onChange={onChangeValue}>
              <input type="radio" value="First" checked={gender === "First"} />{" "}
              1 try  
              <input
                type="radio"
                value="Second"
                checked={gender === "Second"}
              />{" "}
              2 try     
            </div>
          </h3>
          </div>
        <div class="row p-3 d-flex justify-content-center">
          <div class="col p-6 d-flex justify-content-center">
            <StopWatch />
          </div>
        </div>
      </div>
    </div>
  );
}

export default inject("TimerStore", "TeamStore")(observer(Admin));
