import React from "react";
import "./StopWatch.css";
import { observer, inject } from "mobx-react";


function Timer({TimerStore}) {
  return (
    <div className="timer">
      <span className="digits">
        {("0" + Math.floor((TimerStore.totalTime / 60000) % 60)).slice(-2)}:
      </span>
      <span className="digits">
        {("0" + Math.floor((TimerStore.totalTime / 1000) % 60)).slice(-2)}.
      </span>
      <span className="digits mili-sec">
        {("0" + ((TimerStore.totalTime / 10) % 100)).slice(-2)}
      </span>
    </div>
  );
}


export default inject("TimerStore")(observer(Timer));