import React, { useState } from "react";
import ControlButtons from "./ControlButtons";
import { observer, inject } from "mobx-react";

import axios from 'axios';

function StopWatch({TimerStore, TeamStore}) {
  const [isActive, setIsActive] = useState(false);
  const [isPaused, setIsPaused] = useState(true);


  React.useEffect(() => {
    let interval = null;
  
    if (isActive && isPaused === false) {
      interval = setInterval(() => {
        TimerStore.increment();
      }, 10);
    } else {
      clearInterval(interval);
    }
    return () => {
      clearInterval(interval);
    };
  }, [isActive, isPaused]);
  
  const handleStart = () => {
    
    setIsActive(true);
    setIsPaused(false);
    axios.post('http://192.168.1.103:80/timer/start');
  };

  const handleReset = () => {
    
    setIsActive(false);
    TimerStore.setTeamName(TeamStore.getTeamName);
    axios.post('http://192.168.1.103:80/timer/stop');
    if (TeamStore.getTry() == 'First') {
      TimerStore.sendFirstTryData();
    }
    else {
      TimerStore.sendSecondTryData();
    }
    TimerStore.reset();
  };
  
  return (
    <div>
      <ControlButtons
        active={isActive}
        handleStart={handleStart}
        handleReset={handleReset}
      />
    </div>
  );
}
  
export default inject("TimerStore", "TeamStore")(observer(StopWatch));
