import React from "react";
  
export default function ControlButtons(props) {
  const StartButton = (
    <div
         onClick={props.handleStart}>
      <h1>Start</h1>
    </div>
  );
  const ActiveButtons = (
      <div 
           onClick={props.handleReset}>
        <h1>Stop</h1>
      </div>
  );
  
  return (
    <div>
      <div>{props.active ? ActiveButtons : StartButton}</div>
    </div>
  );
}