import {
  action,
  computed,
  makeObservable,
  observable,
  runInAction,
} from "mobx";

import axios from 'axios';

class TeamStore {
  teams = [];

  curTeam = "";
  try = "First";
  timerStatus = 0;

  constructor() {
    makeObservable(this, {
      teams: observable,
      curTeam: observable,
      timerStatus: observable,
      try: observable,
      createTeam: action,
      getTeamName: computed,
      getTry: action,
      cleanTeam: action,
      setTimerStatus: action,
      getTimerStatus: computed,
      hideTeam: action,
      showTeam: action,

    });
    runInAction(this.prefetchData);
  }

  setTimerStatus(status) {
    this.timerStatus = status;
  }

  get getTimerStatus() {
    return this.timerStatus;
  }


  setTeamName(name) {
    this.curTeam = name;
  }

  setTry(data) {
    this.try = data;
  }

  getTry() {
    return this.try;
  }

  get getTeamName() {
    return this.curTeam;
  }

  createTeam(team) {
    this.teams.push(team);
  }

  cleanTeam() {
    this.teams = [];
  }

  hideTeam() {
    console.log(this.getTeamName);
    const data = { name: this.getTeamName};
    axios.post('http://192.168.1.103:80/team/hide', data, {headers: {
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Methods': 'GET,PUT,POST,DELETE,PATCH,OPTIONS',
      'Access-Control-Allow-Headers': "append,delete,entries,foreach,get,has,keys,set,values,Authorization",
    }});
  }

  showTeam() {
    console.log(this.getTeamName);
    const data = { name: this.getTeamName};
    axios.post('http://192.168.1.103:80/team/show', data, {headers: {
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Methods': 'GET,PUT,POST,DELETE,PATCH,OPTIONS',
      'Access-Control-Allow-Headers': "append,delete,entries,foreach,get,has,keys,set,values,Authorization",
    }});
  }

  prefetchData = async () => {
    const res = await axios.get('http://192.168.1.103:80/teams')
    const teams = res.data['results'];
    const resTimer = await axios.get('http://192.168.1.103:80/timer/status')
    const timerStatus = resTimer.data['timer'];
    this.setTimerStatus(timerStatus);

    teams.sort(function(a, b) { 
      return a.id - b.id  ||  a.name.localeCompare(b.name);
    });

    setTimeout(() => {
      this.cleanTeam();
      teams.map((team) => {
        this.createTeam(team);
      });
      this.prefetchData();
    }, 500);
  };
}

export default TeamStore;
