import {
  action,
  computed,
  makeObservable,
  observable,
} from "mobx";


import axios from 'axios';


class TimerStore {
  time = 0;
  teamName = "";


  constructor() {
    makeObservable(this, {
      time: observable,
      teamName: observable,
      increment: action,
      totalTime: computed,
      reset: action,
      sendFirstTryData: action,
      setTeamName: action
    });
  }

  get totalTime() {
    return this.time;
  }

  increment() {
    this.time += 10;
  }

  reset() {
    this.time = 0;
  }

  setTeamName(name) {
    this.teamName = name;
  }

  sendFirstTryData = async () => {
    const data = { name: this.teamName, time: this.time };
    await axios.post('http://192.168.1.103:80/team/first_try', data, {headers: {
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Methods': 'GET,PUT,POST,DELETE,PATCH,OPTIONS',
      'Access-Control-Allow-Headers': "append,delete,entries,foreach,get,has,keys,set,values,Authorization",
    }});
  };

  sendSecondTryData = async () => {
    const data = { name: this.teamName, time: this.time };
    await axios.post('http://192.168.1.103:80/team/second_try', data, {headers: {
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Methods': 'GET,PUT,POST,DELETE,PATCH,OPTIONS',
      'Access-Control-Allow-Headers': "append,delete,entries,foreach,get,has,keys,set,values,Authorization",
    }});
  };
}

export default TimerStore;
