import React, {useState} from "react";
import { observer, inject } from "mobx-react";
import "bootstrap/dist/css/bootstrap.min.css";


function convertMsToString(time) {
  return ("0" + Math.floor((time / 60000) % 60)).slice(-2) + ":" + ("0" + Math.floor((time / 1000) % 60)).slice(-2) + "." + ("0" + ((time / 10) % 100)).slice(-2)
}



function TeamList({ TimerStore, TeamStore}) {
  const [isActive, setIsActive] = useState(false);
  const [isPaused, setIsPaused] = useState(true);

  const handleStart = () => {
    setIsActive(true);
    setIsPaused(false);
  };

  const handleReset = () => {
    setIsActive(false);
    TimerStore.reset();
  };

  if ( TeamStore.getTimerStatus === 1 && !isActive) {
    handleStart();
  }

  if ( TeamStore.getTimerStatus === 0 && isActive) {
    handleReset();
  }

  React.useEffect(() => {
    let interval = null;
  
    if (isActive && isPaused === false) {
      interval = setInterval(() => {
        TimerStore.increment();
      }, 10);
    } else {
      clearInterval(interval);
    }
    return () => {
      clearInterval(interval);
    };
  }, [isActive, isPaused]);
  
  


  return (
          <table class="table table-bordered">
            <thead>
              <tr>
                <th>Place</th>
                <th>Team</th>
                <th>First try</th>
                <th>Second try</th>
                <th>Best time</th>
              </tr>
            </thead>
            <tbody>
              {TeamStore.teams.map((team) => {
                let color = team.id === 1 ? 'table-success' : team.id === 2 ? 'table-warning' : (team.id === 3 ? 'table-danger' : 'table-light');
                return (
                  <tr class={color}>
                    <td>{team.id}</td>
                    <td>{team.team_name}</td>
                    <td>{convertMsToString(team.first_try)}</td>
                    <td>{convertMsToString(team.second_try)}</td>
                    <td>{convertMsToString(team.best_try)}</td>
                  </tr>
                );
              })}
            </tbody>
          </table>
  );
}

export default inject("TimerStore", "TeamStore")(observer(TeamList));
