import Timer from "../components/StopWatch/Timer";
import TeamList from "../components/TeamList";
import { observer, inject } from "mobx-react";
import logo from "./metu.png";
import logo2 from "./logo.png";

function TeamDashBoard({ TimerStorem, TeamStore }) {
  return (
    <div class="container">
      <div class="row d-flex justify-content-center">
        <div class="col p-3 d-flex justify-content-center">
          <img src={logo2} alt="Logo2" width="213" height="72" />
        </div>
        <div class="col p-3 d-flex justify-content-center" >
          <img src={logo} alt="Logo" width="250" height="100" />
        </div>
        <div class="row d-flex justify-content-center">
          <Timer />
        </div>
        <div class="row d-flex justify-content-center">
        <TeamList />
        </div>
      </div>
    </div>
  );
}

export default inject("TimerStore", "TeamStore")(observer(TeamDashBoard));
